<template>
	<footer class="footer">
	    <div class="footer-text">
	        <div class="copyright">
	        	Створено з <img src="../components/footer/icons/heart.svg" alt="любов'ю"> та 
	        	<img src="../components/footer/icons/vuejs.svg" alt="любов'ю">
	        	<br>
	        </div>
	    </div>
	    <!-- /.footer-text -->
	    <!-- /.container -->
    	<div class="enter" v-if='User'>
    		<div  @click='logout' class="logout">
    			Вийти
    			<img src="@/components/auth/icons/sign-out.svg" alt="">
    		</div>
    		<!-- /.logout -->
    	</div>
	</footer>
	<!-- /.footer -->
</template>
<script>
	export default {
		name: 'Footer',
		methods: {
			logout() {
				this.$_auth.signOut()
					.then(() => {
						this.$router.push({path: '/'});
						this.$store.commit('isLogOut');
					})
			}
		},
		computed: {
			User() {
				return this.$store.getters.User;
			}
		},
	}
</script>

